.App {
  text-align: center;
  min-height: 100vh;
  background-color: #FAE7C3;
  font-family: Calibri;
  color: #BD9242;
}

.App-header {
  display: flex;
  flex-direction: column;
  color: #BD9242;
}

main {
  flex-grow: 1;
  text-align: center;
  padding: 5px;
}

footer {
  padding: 5px;
  justify-content: center;
  font-size: medium;
  text-align: center;
  left:0;
  bottom:0;
  right:0;
}

header {
  font-family: 'Bebas Neue', cursive;
  font-size: 60px;
  line-height: 50px;
  padding-top: 40px;
}

a {
  text-decoration: none;
}

a:hover {
  color: #611c7c;
}
